.table {
    position: absolute;
    width: 690px;
    /* width: 60%; */
    /* height: 40%; */
    height: 300px;
    background-color: darkgreen;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 30%;
    border: 10px solid darkred;
  }
